@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.cart{
    width: 100%;
    max-width: 800px;
    margin: 20px auto;
    position: relative;
}
.list{
    padding: 0px 30px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    @include phone{
        padding: 0px 5px;
    }
}
.info{
    margin-bottom: 10px;
    display: flex;
}
.item{
    border-top: 1px solid rgba(170, 170, 170, 0.425);
    padding: 20px;
}
.img{
    object-fit: contain;
    width: 100px;
    height: 96px;
    margin-right: 16px;
}
.actionsItemInfo,.plus,.minus{
    fill: $cart-img;
    min-height: 26px;
    min-width: 26px;
}
.name{
    padding-left: 10px;
    width: 100%;
    max-height: 98px;
    overflow: hidden;
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
    color: #221f1f;
}
.actionsItem{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @include tablet{
        justify-content: space-between;
    }
}
.counter{
    display: flex;
    align-items: center;
}
.price{
    font-size: 22px;
    color: $color-blak;
    font-weight: 600;
    margin-left: 100px;
    @include phone{
        margin-left: 30px;
    }
}
.count{
    padding: 10px 20px;
    margin: 0px 10px;
    border: 1px solid $border-color;
    @include tablet{
        margin: 0px;
    }
}