@import '../../styles/mixin.scss';
.main{
    padding-top: 15px;
    padding-left: 15px;
    width: 80%;
    @include laptop{
        width: 100%;
        margin: 0 auto;
        padding-top: 0px;
        padding-left: 0px;
    }
}
.container{
    display: flex;
    flex-wrap: wrap;
    padding-left: 10px;
    @include laptop{
        justify-content: center;
        gap: 10px;
    }
    @include phone{
        gap: 10px;
    }
}
.item{
    padding-top: 20px;
    width: 170px;
    align-items: center;
    height: 280px;
    border-radius: 15px;
    @include phone{
        width: 140px;
        height: 250px;
        margin-top: 15px;
    }
}
.img{
    width: 100%;
    height: 160px;
    object-fit: contain;
    @include phone{
        height: 130px;
    }
}
.heart{
    fill: red;
    height: 30px;
    width: 30px;
}
.text{
    margin-top: 10px;
}
.name{
    display: block;
    height: 32px;
    margin-bottom: 8px;
    overflow: hidden;
    font-size: 16px;
    line-height: 16px;
    color: #221f1f;
}
.price{
    font-size: 23px;
    color: #221f1f
}
.actions{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    padding-right: 20px;
}
.btn{
    font-size: 16px;
    color: #fff;
    font-weight: 600;
    padding: 5px 30px;
    border: none;
    border-radius: 10px;
    background-color: rgb(39, 41, 189);
    transition: .2s;
    &:hover{
        background-color: rgb(19, 23, 214) ;
    }
    &:active{
        border: 1px solid rgb(0, 4, 255);
    }
}
